header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.me img {
    width: 20rem;
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    margin-top: 4rem;
    border-radius: 12rem;
    overflow: hidden;
}

.scroll__down {
    position: fixed;
    right: 3.5rem;
    bottom: 2rem;
    font-size: 2rem;
}

@media screen and (max-width: 1024px) {
    .header {
        height: 68vh;
    }
}

@media screen and (max-width: 600px) {
    .header {
        height: 100vh;
    }

    .header__socials, .scroll_down {
        display: none;
    }
    
}